<template>
  <div class="questionDetail_container">
      <head-top :head-title="title" goBack="true"></head-top>
      <section class="questionDetail_tx">
          {{tx}}
      </section>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import headTop from '../../../components/header/header'
export default {
    components: {
        headTop
    },computed: {
        ...mapState([
            'title','tx'
        ])
    }
}
</script>

<style lang="scss" scopde>
@import '../../../style/mixin.scss';
.questionDetail_container
{
    overflow-y: auto;
    @include zi;
    padding-top: 1.95rem;
}
.questionDetail_tx{
    padding:0.5rem;
    @include sc(0.6rem,#333);
}
</style>



